export * from './prepareDictionaries'

let dictionaries = {}

const setData = (data) => {
  dictionaries = data
}

export const setDictionaries = (data) => {
  setData(data)
}

export const getDictionaryByCode = (space, code) => {
  if (dictionaries[space]) {
    const elem = dictionaries[space].find((item) => item.id === code)

    if (elem) {
      return elem.label
    }
  }

  return code
}

export const getDictionary = (space) => {
  return dictionaries[space] || []
}
