export const prepareTickets = (tickets) => {
  if (tickets) {
    return tickets.map((ticket) => {
      const { subject, messages, status, created, id } = ticket
      const nextTicket = {
        subject,
        status,
        created,
        id,
        message: {},
      }

      if (messages && Array.isArray(messages) && messages[0]) {
        const { text, author = {}, files, created } = messages[0]
        let lastMessage = {
          text,
          author,
          created,
        }

        if (!text && Array.isArray(files) && files[0]) {
          lastMessage.text = files[0].name
        }

        nextTicket.message = lastMessage
      }

      return nextTicket
    })
  }
  return []
}

export default prepareTickets
