import { offer } from 'src/rest'
import { offerActions } from 'src/actions'
import { to } from 'src/lib'

const loadLink = (id) => async (dispatch, state) => {
  dispatch(offerActions.loadLinkStart())

  const { value } = await to(offer.link(id))
  if (value && value.data) {
    dispatch(offerActions.loadLinkSuccess(value.data))
  } else {
    dispatch(offerActions.loadLinkFailure())
  }
}

export default loadLink
