import { commonConstants } from 'src/constants'

export const prepareDictionaries = (dictionaries) => {
  const { AVAILABLE_DICTIONARIES } = commonConstants
  let data = {}

  AVAILABLE_DICTIONARIES.forEach((key) => {
    let nextData = []
    if (dictionaries && dictionaries[key]) {
      nextData = dictionaries[key].map((item) => {
        return {
          id: item.code,
          label: item.name,
          value: item.code,
        }
      })
    }

    data[key] = nextData
  })

  return data
}
export default prepareDictionaries
