import { affiliates } from 'src/rest'
import { affiliatesActions } from 'src/actions'
import { to } from 'src/lib'

const loadAffiliate = (id) => async (dispatch) => {
  dispatch(affiliatesActions.loadAffiliateStart())

  const { value, error } = await to(affiliates.affiliate(id))

  if (value && value.data) {
    dispatch(affiliatesActions.loadAffiliateSuccess(value.data))
  } else {
    dispatch(affiliatesActions.loadAffiliateFailure())
  }
}

export default loadAffiliate
