import { offers } from 'src/rest'
import { offersActions } from 'src/actions'
import { to } from 'src/lib'

const loadOffers = () => async (dispatch) => {
  dispatch(offersActions.loadOffersStart())

  const { value } = await to(offers.offers())

  if (value && value.data) {
    dispatch(offersActions.loadOffersSuccess(value.data))
  } else {
    dispatch(offersActions.loadOffersFailure())
  }
}

export default loadOffers
