import { createAction } from 'redux-actions'

const prefix = 'NOTIFICATIONS:'

export const loadNotificationsSuccess = createAction(prefix + 'LOAD_SUCCESS')
export const loadNotificationsFailure = createAction(prefix + 'LOAD_FAILURE')

export const removeNotificationsSuccess = createAction(prefix + 'REMOVE_SUCCESS')
export const removeNotificationsFailure = createAction(prefix + 'REMOVE_FAILURE')

export const removeAllNotificationsSuccess = createAction(prefix + 'REMOVE_ALL_SUCCESS')
export const removeAllNotificationsFailure = createAction(prefix + 'REMOVE_ALL_FAILURE')
