import { notifications } from 'src/rest'
import { notificationsActions } from 'src/actions'
import { to } from 'src/lib'

import loadNotifications from './loadNotifications'

const removeAllNotifications = () => async (dispatch) => {
  const { value, error } = await to(notifications.removeAllNotifications())

  if (value) {
    dispatch(notificationsActions.removeAllNotificationsSuccess())
    dispatch(loadNotifications())
  } else {
    dispatch(notificationsActions.removeAllNotificationsFailure())
  }
}

export default removeAllNotifications
