import { offer } from 'src/rest'
import { offerActions } from 'src/actions'
import { to } from 'src/lib'

const loadOffer = (id) => async (dispatch, state) => {
  dispatch(offerActions.loadOfferStart())

  const { value } = await to(offer.offer(id))
  if (value && value.data) {
    dispatch(offerActions.loadOfferSuccess(value.data))
  } else {
    dispatch(offerActions.loadOfferFailure())
  }
}

export default loadOffer
