import { createAction } from 'redux-actions'

const prefix = 'STATS:'

export const loadStatsStart = createAction(prefix + 'LOAD_STATS_START')
export const loadStatsSuccess = createAction(prefix + 'LOAD_STATS_SUCCESS')
export const loadStatsFailure = createAction(prefix + 'LOAD_STATS_FAILURE')

export const loadStatsTimeStart = createAction(prefix + 'LOAD_STATS_TIME_START')
export const loadStatsTimeSuccess = createAction(prefix + 'LOAD_STATS_TIME_SUCCESS')
export const loadStatsTimeFailure = createAction(prefix + 'LOAD_STATS_TIME_FAILURE')

export const loadStatsCountryStart = createAction(prefix + 'LOAD_STATS_COUNTRY_START')
export const loadStatsCountrySuccess = createAction(prefix + 'LOAD_STATS_COUNTRY_SUCCESS')
export const loadStatsCountryFailure = createAction(prefix + 'LOAD_STATS_COUNTRY_FAILURE')
