const getState = (state) => state.user

export const getIsLogged = (state) => getState(state).isLogged
export const getIsLoggingIn = (state) => getState(state).isLoggingIn
export const getUserType = (state) => getState(state).type
export const getUserInfo = (state) => getState(state).info
export const getIsAdmin = (state) => getState(state).type === 'admin'
export const getIsManager = (state) => getState(state).type === 'manager'
export const getIsAffiliate = (state) => getState(state).type === 'affiliate'
