import { handleActions, combineActions } from 'redux-actions'

import { affiliatesActions } from 'src/actions'

export const initialState = {
  contacts: {},
  name: {},
  isLoading: false,
  isError: false,
}

const reducerMap = {
  [combineActions(affiliatesActions.loadAffiliateStart)]: (state) => ({ ...state, isLoading: true, isError: false }),
  [combineActions(affiliatesActions.loadAffiliateSuccess)]: (state, action) => ({
    ...action.payload,
    isLoading: false,
    isError: false,
  }),
  [combineActions(affiliatesActions.loadAffiliateFailure)]: () => ({ ...initialState, isError: true }),
}

export default handleActions(reducerMap, initialState)
