const getState = (state) => state.statistics

export const getStatistics = (state) => getState(state)
export const getStatisticsCountries = (state) => getState(state).countries
export const getStatisticsFilter = (state) => getState(state).filter
export const getStatisticsIsLoading = (state) => getState(state).isLoading
export const getStatisticsTimes = (state) => getState(state).times
export const getStatisticsTotals = (state) => getState(state).totals
