import { handleActions, combineActions } from 'redux-actions'

import { affiliatesActions } from 'src/actions'

export const initialState = []

const reducerMap = {
  [combineActions(affiliatesActions.loadAffiliatesSuccess)]: (state, action) => action.payload,
  [combineActions(affiliatesActions.loadAffiliatesFailure)]: () => initialState,
}

export default handleActions(reducerMap, initialState)
