import { user } from 'src/rest'
import { userActions } from 'src/actions'
import { to } from 'src/lib'

import loadProfile from './loadProfile'

const updateProfile = (data) => async (dispatch) => {
  dispatch(userActions.updateStart())

  const { value, error } = await to(user.updateProfile(data))

  if (value) {
    dispatch(userActions.updateSuccess())

    dispatch(loadProfile())
  } else {
    dispatch(userActions.updateFailure())
  }
}

export default updateProfile
