import React, { memo, useMemo } from 'react'
import cn from 'classnames'
import { NavLink, useLocation } from 'react-router-dom'

import { ReactComponent as FeedbackIcon } from 'src/assets/images/icons/feedback.svg'
import { ReactComponent as LogoIcon } from 'src/assets/images/logo-white.svg'
import Clock from './components/Clock'

import styles from './styles.module.scss'
import icons from 'src/assets/images/sidebar'
import { useMappedState } from './bindings'

const menuConfig = {
  STATISTICS: {
    name: 'Statistics',
    to: '/statistics',
    icon: 'StatisticsIcon',
  },
  FINANCE: {
    name: 'Finance',
    to: '/finance',
    icon: 'FinanceIcon',
  },
  INTEGRATION: {
    name: 'Integration',
    to: '/integration',
    icon: 'IntegrationIcon',
  },
  SUPPORT: {
    name: 'Support',
    to: '/support',
    icon: 'SupportIcon',
  },
  OFFERS: {
    name: 'Offers',
    to: '/offers',
    icon: 'OffersIcon',
  },
  SETTINGS: {
    name: 'Settings',
    to: '/settings',
    icon: 'SettingsIcon',
  },
}

const affiliateMenu = ['STATISTICS', 'FINANCE', 'INTEGRATION', 'SUPPORT', 'OFFERS', 'SETTINGS']
const managerMenu = ['STATISTICS', 'SUPPORT', 'OFFERS']

const SideBarRaw = () => {
  const { pathname } = useLocation()
  const { isManager } = useMappedState()

  const date = useMemo(() => {
    var utc = new Date().toJSON().slice(0, 10).replace(/-/g, '-')
    return utc
  }, [])

  const renderMenu = useMemo(() => {
    const menu = isManager ? managerMenu : affiliateMenu
    return menu.map((item) => {
      const { name, to, icon } = menuConfig[item]
      const isActive = pathname.includes(to)
      const Icon = icons[icon]

      return (
        <li className={styles.item} key={item}>
          <NavLink
            to={to}
            className={cn(styles.link, {
              [styles.linkActive]: isActive,
            })}
          >
            <div className={styles.itemIcon}>
              <Icon />
            </div>
            <div className={styles.itemText}>{name}</div>
          </NavLink>
        </li>
      )
    })
  }, [pathname, isManager])

  return (
    <aside className={styles.sidebar}>
      <div className={styles.head}>
        <div className={styles.logo}>
          <LogoIcon />
        </div>
        <div className={styles.time}>
          <Clock />
        </div>
        <div className={styles.date}>{date}</div>
      </div>
      <div className={styles.body}>
        <ul className={styles.list}>{renderMenu}</ul>
      </div>

      <div className={styles.footer}>
        <div className={styles.feedback}>
          <div className={styles.feedbackHead}>
            <div className={styles.feedbackTitle}>Feedback</div>
            <div className={styles.feedbackIcon}>
              <FeedbackIcon />
            </div>
          </div>
          <div className={styles.feedbackDescr}>Service improvement suggestions</div>
        </div>
      </div>
    </aside>
  )
}

export const SideBar = memo(SideBarRaw)
export default SideBar
