import { createAction } from 'redux-actions'

const prefix = 'OFFER:'

export const loadOfferStart = createAction(prefix + 'LOAD_START')
export const loadOfferSuccess = createAction(prefix + 'LOAD_SUCCESS')
export const loadOfferFailure = createAction(prefix + 'LOAD_FAILURE')

export const loadMediaStart = createAction(prefix + 'LOAD_MEDIA_START')
export const loadMediaSuccess = createAction(prefix + 'LOAD_MEDIA_SUCCESS')
export const loadMediaFailure = createAction(prefix + 'LOAD_MEDIA_FAILURE')
