import { handleActions } from 'redux-actions'

import { offersActions } from 'src/actions'

const defaultState = {
  data: null,
  isLoading: false,
}

const reducersMap = {
  [offersActions.loadOffersStart]: (state, action) => ({
    ...state,
    isLoading: true,
  }),
  [offersActions.loadOffersSuccess]: (state, action) => ({
    data: action.payload,
    isLoading: false,
  }),
  [offersActions.loadOffersFailure]: (state, action) => ({
    data: action.payload,
    isLoading: false,
  }),
}

export default handleActions(reducersMap, defaultState)
