import { affiliates } from 'src/rest'
import { affiliatesActions } from 'src/actions'
import { to } from 'src/lib'

const loadAffiliateStats = (id, params) => async (dispatch) => {
  dispatch(affiliatesActions.loadAffiliateStatsStart())
  const { value, error } = await to(affiliates.affiliateStats(id, params))

  if (value && value.data) {
    dispatch(affiliatesActions.loadAffiliateStatsSuccess(value.data))
  } else {
    dispatch(affiliatesActions.loadAffiliateStatsFailure())
  }
}

export default loadAffiliateStats
