import { handleActions, combineActions } from 'redux-actions'

import { userActions, authActions } from 'src/actions'

export const initialState = {
  contacts: {},
  name: {},
}

const reducerMap = {
  [combineActions(userActions.loadSuccess, authActions.loginSuccess)]: (state, action) => action.payload,
  [combineActions(userActions.loadFailure, authActions.loginFailure)]: () => initialState,
}

export default handleActions(reducerMap, initialState)
