import { instance } from '../axiosInstance'
import { API_GATE } from '../constants'

export const createTicket = (data) => {
  return instance({
    method: 'post',
    url: `${API_GATE}/tickets`,
    data,
  })
}

export const loadTickets = () => {
  return instance({
    method: 'get',
    url: `${API_GATE}/tickets`,
  })
}

export const openTicket = (ticket_id) => {
  return instance({
    method: 'get',
    url: `${API_GATE}/tickets/${ticket_id}`,
  })
}

export const sendTicketMessage = (ticket_id, data) => {
  return instance({
    method: 'post',
    url: `${API_GATE}/tickets/${ticket_id}`,
    data,
  })
}

export const closeTicket = (ticket_id) => {
  return instance({
    method: 'delete',
    url: `${API_GATE}/tickets/${ticket_id}`,
  })
}
