import { affiliates } from 'src/rest'
import { affiliatesActions } from 'src/actions'
import { to } from 'src/lib'

const enableAffiliate = (id) => async (dispatch) => {
  dispatch(affiliatesActions.enableAffiliateStart())
  const { value, error } = await to(affiliates.enableAffiliate(id))

  if (value && value.data) {
    dispatch(affiliatesActions.enableAffiliateSuccess())
  } else {
    dispatch(affiliatesActions.enableAffiliateFailure())
  }
}

export default enableAffiliate
