import { user } from 'src/rest'
import { userActions } from 'src/actions'
import { to } from 'src/lib'

const loadProfile = () => async (dispatch) => {
  dispatch(userActions.loadStart())
  const { value, error } = await to(user.profile())

  if (value && value.data) {
    dispatch(userActions.loadSuccess(value.data))
  } else {
    dispatch(userActions.loadFailure())
  }
}

export default loadProfile
