import { handleActions, combineActions } from 'redux-actions'

import { statsActions } from 'src/actions'

export const initialState = []

const reducerMap = {
  [combineActions(statsActions.loadStatsSuccess)]: (state, action) => {
    console.log('loadStatsSuccess action.payload', action.payload)
    return action.payload.countryItems
  },
}

export default handleActions(reducerMap, initialState)
