import { handleActions, combineActions } from 'redux-actions'

import { ticketsActions } from 'src/actions'

export const initialState = {}

const reducerMap = {
  [combineActions(ticketsActions.loadTicketSuccess)]: (state, action) => action.payload,
  [combineActions(ticketsActions.loadTicketFailure)]: () => initialState,
}

export default handleActions(reducerMap, initialState)
