import { handleActions } from 'redux-actions'

import { offerActions } from 'src/actions'

const defaultState = {
  offer: {
    isLoading: false,
  },
  media: {
    data: [],
    isLoading: false,
  },
}

const reducersMap = {
  [offerActions.loadOfferStart]: (state, action) => ({
    ...state,
    offer: {
      ...state.offer,
      isLoading: true,
    },
  }),
  [offerActions.loadOfferSuccess]: (state, action) => ({
    ...state,
    offer: {
      ...action.payload,
      isLoading: false,
    },
  }),
  [offerActions.loadOfferFailure]: (state, action) => ({
    ...state,
    offer: {
      ...state.offer,
      isLoading: false,
    },
  }),
  [offerActions.loadMediaStart]: (state, action) => ({
    ...state,
    media: {
      ...state.media,
      isLoading: true,
    },
  }),
  [offerActions.loadMediaSuccess]: (state, action) => ({
    ...state,
    media: {
      data: action.payload,
      isLoading: false,
    },
  }),
  [offerActions.loadMediaFailure]: (state, action) => ({
    ...state,
    media: {
      ...state.media,
      isLoading: false,
    },
  }),
}

export default handleActions(reducersMap, defaultState)
