import { statsActions } from 'src/actions'

import loadStatsCountry from './loadStatsCountry'
import loadStatsTime from './loadStatsTime'

const loadStats = (params) => async (dispatch) => {
  dispatch(statsActions.loadStatsStart())
  const payload = {
    countryItems: [],
    timeItems: [],
    totals: {},
  }

  const { value: valueCountry, error: errorCountry } = await loadStatsCountry(params)
  const { value: valueTime, error: errorTime } = await loadStatsTime(params)

  if (valueCountry) {
    const { items: countryItems } = valueCountry.data
    payload.countryItems = countryItems
  }

  if (valueTime) {
    const { items: timeItems, totals } = valueTime.data
    payload.timeItems = timeItems
    payload.totals = totals
  }

  dispatch(statsActions.loadStatsSuccess(payload))
}

export default loadStats
