import { handleActions, combineActions } from 'redux-actions'

import { ticketsActions } from 'src/actions'

export const initialState = false

const reducerMap = {
  [combineActions(ticketsActions.loadTicketsStart)]: () => true,
  [combineActions(ticketsActions.loadTicketsSuccess, ticketsActions.loadTicketsFailure)]: () => initialState,
}

export default handleActions(reducerMap, initialState)
