import { instance } from '../axiosInstance'
import { API_GATE } from '../constants'

export const offer = (id) => {
  return instance({
    method: 'get',
    url: `${API_GATE}/offers/${id}`,
  })
}

export const link = (id) => {
  return instance({
    method: 'get',
    url: `${API_GATE}/offers/${id}/link`,
  })
}

export const media = (id) => {
  return instance({
    method: 'get',
    url: `${API_GATE}/offers/${id}/media`,
  })
}
