import { offer } from 'src/rest'
import { offerActions } from 'src/actions'
import { to } from 'src/lib'

const loadMedia = (id) => async (dispatch, state) => {
  dispatch(offerActions.loadMediaStart())

  const { value } = await to(offer.media(id))
  if (value && value.data) {
    dispatch(offerActions.loadMediaSuccess(value.data))
  } else {
    dispatch(offerActions.loadMediaFailure())
  }
}

export default loadMedia
