import { instance } from '../axiosInstance'
import qs from 'query-string'
import { API_GATE } from '../constants'

const getQuery = (params) => {
  let query = qs.stringify(params, {
    skipNull: true,
    skipEmptyString: true,
    arrayFormat: 'bracket-separator',
    arrayFormatSeparator: ',',
  })

  return query.replace('to', 'date[to]').replace('from', 'date[from]')
}

export const statsTime = (params) => {
  let query = getQuery(params)
  return instance({
    method: 'get',
    url: `${API_GATE}/stats/time${query && '?' + query}`,
  })
}

export const statsCountry = (params) => {
  let query = getQuery(params)
  return instance({
    method: 'get',
    url: `${API_GATE}/stats/country${query && '?' + query}`,
  })
}
