const getState = (state) => state.affiliate

export const getAffiliate = (state) => getState(state)
export const getAffiliateInfo = (state) => getState(state).info
export const getAffiliateInfoIsLading = (state) => getAffiliateInfo(state).isLoading
export const getAffiliateInfoIsError = (state) => getAffiliateInfo(state).isError

export const getAffiliateMetrics = (state) => getState(state).metrics
export const getAffiliateMetricsItems = (state) => getAffiliateInfo(state).items
export const getAffiliateMetricsIsLading = (state) => getAffiliateInfo(state).isLoading
export const getAffiliateMetricsIsError = (state) => getAffiliateInfo(state).isError
