const getState = (state) => state.offer

export const getOffer = (state) => getState(state).offer
export const getOfferIsLoading = (state) => getOffer(state).isLoading

export const getMedia = (state) => getState(state).media
export const getMediaIsLoading = (state) => getMedia(state).isLoading
export const getMediaData = (state) => getMedia(state).data
