import { createAction } from 'redux-actions'

const prefix = 'USER:'

export const loadStart = createAction(prefix + 'LOAD_START')
export const loadSuccess = createAction(prefix + 'LOAD_SUCCESS')
export const loadFailure = createAction(prefix + 'LOAD_FAILURE')

export const updateStart = createAction(prefix + 'UPDATE_START')
export const updateSuccess = createAction(prefix + 'UPDATE_SUCCESS')
export const updateFailure = createAction(prefix + 'UPDATE_FAILURE')
