import { affiliates } from 'src/rest'
import { affiliatesActions } from 'src/actions'
import { to } from 'src/lib'

const disableAffiliate = (id, params) => async (dispatch) => {
  dispatch(affiliatesActions.disableAffiliateStart())
  const { value, error } = await to(affiliates.disableAffiliate(id, params))

  if (value && value.data) {
    dispatch(affiliatesActions.disableAffiliateSuccess())
  } else {
    dispatch(affiliatesActions.disableAffiliateFailure())
  }
}

export default disableAffiliate
