import { handleActions, combineActions } from 'redux-actions'

import { statsActions } from 'src/actions'

export const initialState = false

const reducerMap = {
  [combineActions(statsActions.loadStatsStart)]: () => true,
  [combineActions(statsActions.loadStatsSuccess, statsActions.loadStatsFailure)]: () => false,
}

export default handleActions(reducerMap, initialState)
