import { tickets } from 'src/rest'
import { ticketsActions } from 'src/actions'
import * as services from 'src/services'
import { to } from 'src/lib'

const getData = () => {
  var array2 = [1, 2, 3, 4, 5, 6, 7, 8, 9]
  return array2.map((item, index) => {
    return {
      subject: `Verification of your payment data. ${index}`,
      messages: [
        {
          text: ` ${index} Dear Partner, In order to comply with the rules compl. Dear Partner, In order to comply with the rules compl. Dear Partner, In order to comply with the rules compl. Dear Partner, In order to comply with the rules compl.`,
          files: [
            {
              name: `string file ${index}`,
              mime: 'image/png',
              link: 'https://www.callcentrehelper.com/images/stories/2017/11/live-chat-symbol-760.png',
            },
          ],
          author: {
            id: `854a312b-ae6a-4d2d-8224-d912a9ddb5a7-${index % 2 ? 'manager' : 'user'}`,
            name: index % 2 ? 'manager' : 'user',
          },
          created: `2020-0${item}-01T12:00:00.000Z`,
        },
      ],
      status: index % 3 ? 'opened' : 'closed',
      id: `854a312b-ae6a-4d2d-8224${index}`,
      created: `2020-0${item}-01T12:00:00.000Z`,
    }
  })
}

const loadTickets = () => async (dispatch) => {
  dispatch(ticketsActions.loadTicketsStart())

  const { value, error } = await to(tickets.loadTickets())

  dispatch(ticketsActions.loadTicketsSuccess(services.prepareTickets(getData())))
  // if (value && value.data) {
  //   dispatch(ticketsActions.loadTicketsSuccess())
  // } else {
  //   dispatch(ticketsActions.loadTicketsFailure())
  // }
}

export default loadTickets
