import { handleActions, combineActions } from 'redux-actions'

import { notificationsActions } from 'src/actions'

export const initialState = []

const reducerMap = {
  [combineActions(notificationsActions.loadNotificationsSuccess)]: (state, action) => action.payload,
  [combineActions(notificationsActions.loadNotificationsFailure)]: () => initialState,
}

export default handleActions(reducerMap, initialState)
