import React, { memo, useCallback } from 'react'
import { Form } from 'react-final-form'

import BaseButton from 'src/components/BaseButton'
import FullLoader from 'src/components/FullLoader'
import InputWithForm from 'src/forms/InputWithForm'
import InputDropzoneForm from 'src/forms/InputDropzoneForm'
import InputRow from 'src/forms/InputRow'

import TextareaWithForm from 'src/forms/TextareaWithForm'

import Modal from '../Modal'
import { useMappedActions } from './bindings'

import validate from './validate'
import styles from './styles.module.scss'

const CreateTicketRaw = (props) => {
  const { onClose } = props
  const actions = useMappedActions()

  const onClickBack = () => {
    onClose()
  }

  const onSubmit = useCallback(
    async (values) => {
      if (values.persist) {
        values.persist()
      } else {
        const errors = await actions.createTicket(values)

        // if (!errors) {
        //   onClose()
        // }
      }
    },
    [actions, onClose],
  )

  return (
    <Modal title="Open new ticket" onClose={onClose} dialogStyles={styles.dialog}>
      <Form
        validate={validate}
        onSubmit={onSubmit}
        render={({ handleSubmit, submitting, pristine, invalid }) => {
          return (
            <form onSubmit={handleSubmit} noValidate>
              {submitting && <FullLoader hasText />}
              <div className={styles.content}>
                <InputRow>
                  <InputWithForm name="subject" label="Subject" placeholder="Enter the subject" />
                </InputRow>
                <InputRow>
                  <TextareaWithForm label="Description" name="description" />
                </InputRow>
                <InputRow>
                  <InputDropzoneForm label="Attach files" placeholder="Drag & Drop your files here" name="file" />
                </InputRow>
              </div>

              <div className={styles.action}>
                <div className={styles.actionLeft}>
                  <BaseButton text="Back" skin="white" type="button" disabled={false} onClick={onClickBack} />
                </div>

                <div className={styles.actionRight}>
                  <BaseButton text="Create ticket" skin="yellow" type="submit" disabled={false} onClick={onSubmit} />
                </div>
              </div>
            </form>
          )
        }}
      />
    </Modal>
  )
}

export const CreateTicket = memo(CreateTicketRaw)
export default CreateTicket
