import { instance } from '../axiosInstance'
import { API_GATE } from '../constants'

export const profile = () => {
  return instance({
    method: 'get',
    url: `${API_GATE}/profile`,
  })
}

export const updateProfile = (data) => {
  return instance({
    method: 'post',
    url: `${API_GATE}/profile`,
    data,
  })
}
