import * as services from 'src/services'

import loadCountries from './loadCountries'
import loadRegions from './loadRegions'
import loadCurrencies from './loadCurrencies'
import loadMetrics from './loadMetrics'
import loadRules from './loadRules'
import loadBrands from './loadBrands'

const loadDictionaries = async () => {
  const [currencies, countries, regions, metrics, rules, brands] = await Promise.all([
    loadCurrencies(),
    loadCountries(),
    loadRegions(),
    loadMetrics(),
    loadRules(),
    loadBrands(),
  ])

  const nextData = services.prepareDictionaries({
    currencies,
    countries,
    regions,
    metrics,
    rules,
    brands,
  })

  services.setDictionaries(nextData)
}

export default loadDictionaries
