import { handleActions, combineActions } from 'redux-actions'

import { affiliatesActions } from 'src/actions'

export const initialState = false

const reducerMap = {
  [combineActions(affiliatesActions.loadAffiliatesStart)]: () => true,
  [combineActions(affiliatesActions.loadAffiliatesSuccess, affiliatesActions.loadAffiliatesFailure)]: () => false,
}

export default handleActions(reducerMap, initialState)
