import { useEffect } from 'react'
import hasIn from 'lodash/hasIn'
import get from 'lodash/get'
import throttle from 'lodash/throttle'

const getDomElement = (element) => {
  let domElement = null

  if (hasIn(element, 'addEventListener')) {
    domElement = element
  } else if (hasIn(element, 'current')) {
    domElement = get(element, 'current')
  } else if (typeof element === 'function') {
    domElement = element()
  }

  return domElement
}

export const useOnScrollElement = ({ element, onScroll }) => {
  useEffect(() => {
    const domElement = getDomElement(element)
    if (domElement) {
      const handleScroll = throttle((e) => onScroll(e), 80)
      domElement.addEventListener('scroll', handleScroll, { passive: true })
      return () => {
        domElement.removeEventListener('scroll', handleScroll)
      }
    }
  }, [element, onScroll])
}

export default useOnScrollElement
