export const prepareTicket = (ticket) => {
  if (ticket) {
    if (Array.isArray(ticket) && ticket[0]) {
      const { subject, messages, status, created, id } = ticket[0]
      const nextTicket = {
        subject,
        status,
        created,
        id,
        messages,
      }

      return nextTicket
    }
  }

  return {}
}

export default prepareTicket
