import { notifications } from 'src/rest'
import { notificationsActions } from 'src/actions'
import { to } from 'src/lib'

import loadNotifications from './loadNotifications'

const removeNotifications = (id) => async (dispatch) => {
  const { value, error } = await to(notifications.removeNotifications(id))

  if (value) {
    dispatch(notificationsActions.removeNotificationsSuccess())
    dispatch(loadNotifications())
  } else {
    dispatch(notificationsActions.removeNotificationsFailure())
  }
}

export default removeNotifications
