import { instance } from '../axiosInstance'
import { API_GATE } from '../constants'

export const loadNotifications = () => {
  return instance({
    method: 'get',
    url: `${API_GATE}/notifications`,
  })
}

export const removeAllNotifications = () => {
  return instance({
    method: 'delete',
    url: `${API_GATE}/notifications`,
  })
}

export const removeNotifications = (notification_id) => {
  return instance({
    method: 'delete',
    url: `${API_GATE}/notifications/${notification_id}`,
  })
}
