const getState = (state) => state.tickets

export const getAll = (state) => getState(state).all
export const getTickets = (state) => getAll(state).data
export const getIsLoadingTickets = (state) => getAll(state).isLoading

export const getTicket = (state) => getState(state).ticket
export const getTicketData = (state) => getAll(state).data
export const getIsLoadingTicket = (state) => getAll(state).isLoading
export const getIsSendingMessage = (state) => getAll(state).isSendingMessage

export const getIsClosingTicket = (state) => getState(state).isClosingTicket
