import { handleActions, combineActions } from 'redux-actions'

import { affiliatesActions } from 'src/actions'

export const initialState = {
  items: [],
  isLoading: false,
  isError: false,
}

const reducerMap = {
  [combineActions(affiliatesActions.loadAffiliateStatsStart)]: (state) => ({
    ...state,
    isLoading: true,
    isError: false,
  }),
  [combineActions(affiliatesActions.loadAffiliateStatsSuccess)]: (state, action) => ({
    ...action.payload,
    isLoading: false,
    isError: false,
  }),
  [combineActions(affiliatesActions.loadAffiliateStatsFailure)]: () => ({ ...initialState, isError: true }),
}

export default handleActions(reducerMap, initialState)
