import { to } from 'src/lib'
import { affiliates } from 'src/rest'
import { affiliatesActions } from 'src/actions'

const loadAffiliates = () => async (dispatch) => {
  dispatch(affiliatesActions.loadAffiliatesStart())
  const { value, error } = await to(affiliates.affiliates())

  if (value && value.data) {
    dispatch(affiliatesActions.loadAffiliatesSuccess(value.data))
  } else {
    dispatch(affiliatesActions.loadAffiliatesFailure())
  }
}

export default loadAffiliates
