import React, { memo, useMemo, useCallback } from 'react'
import Slider from 'react-slick'

import { ReactComponent as ArrowIcon } from 'src/assets/images/icons/slider_arrow.svg'
import { ReactComponent as CloseIcon } from 'src/assets/images/icons/popup_close.svg'

import Modal from '../Modal'
import styles from './styles.module.scss'

const GalleryRaw = (props) => {
  const { onClose, images } = props

  const nextArrow = useMemo(() => {
    return (
      <button className={styles.btn}>
        <ArrowIcon className={styles.btnIconNext} />
      </button>
    )
  }, [])
  const prevArrow = useMemo(() => {
    return (
      <button className={styles.btn}>
        <ArrowIcon className={styles.btnIconPrew} />
      </button>
    )
  }, [])

  const onCrossClick = useCallback(() => {
    onClose()
  }, [onClose])

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    nextArrow,
    prevArrow,
  }

  const renderSlide = useMemo(() => {
    return images.map((image) => {
      return (
        <div className={styles.item}>
          <img src={image.link} alt="" />
        </div>
      )
    })
  }, [images])

  return (
    <Modal title="Edit preset" onClose={onClose} dialogStyles={styles.dialog} hideHead>
      <button className={styles.close} onClick={onCrossClick} type="button">
        <CloseIcon />
      </button>
      <Slider {...settings}>{renderSlide}</Slider>
    </Modal>
  )
}

export const Gallery = memo(GalleryRaw)
export default Gallery
