import React, { memo, Fragment } from 'react'
import PropTypes from 'prop-types'

import SideBar from 'src/indyComponents/SideBar'
import { useMappedState } from './bindings'
import styles from './styles.module.scss'

const DefaultLayoutRaw = ({ children }) => {
  const { isLogged } = useMappedState()

  if (!isLogged) {
    return React.Children.map(children, (child) => React.cloneElement(child))
  }

  return (
    <Fragment>
      <div className={styles.main}>
        <div className={styles.sidebar}>
          <SideBar />
        </div>
        <div className={styles.body}>{children}</div>
      </div>
    </Fragment>
  )
}

DefaultLayoutRaw.prototype = {
  children: PropTypes.node,
}

export const DefaultLayout = memo(DefaultLayoutRaw)
export default DefaultLayout
