import qs from 'query-string'
import { instance } from '../axiosInstance'
import { API_GATE } from '../constants'

const getQuery = (params) => {
  let query = qs.stringify(params, {
    skipNull: true,
    skipEmptyString: true,
    arrayFormat: 'bracket-separator',
    arrayFormatSeparator: ',',
  })

  return query.replace('to', 'date[to]').replace('from', 'date[from]')
}

export const affiliates = () => {
  return instance({
    method: 'get',
    url: `${API_GATE}/affiliates`,
  })
}

export const affiliate = (id) => {
  return instance({
    method: 'get',
    url: `${API_GATE}/affiliates/${id}`,
  })
}

export const affiliateStats = (id, params) => {
  let query = getQuery(params)
  return instance({
    method: 'get',
    url: `${API_GATE}/affiliates/${id}/stats${query && '?' + query}`,
  })
}

export const enableAffiliate = (id) => {
  return instance({
    method: 'post',
    url: `${API_GATE}/affiliates/${id}/enable`,
  })
}

export const disableAffiliate = (id) => {
  return instance({
    method: 'post',
    url: `${API_GATE}/affiliates/${id}/disable`,
  })
}
